import React, { Component } from 'react';
import './App.css';
import SvgComponent from './components/SvgComponent';
import * as d3 from "d3";
import ObjectSvg from "./components/ObjectSvg/ObjectSvg"
import attachEventListeners from './attachListner';

class App extends Component {

  constructor(props){
    super(props)
    this.state = {
      idDetails:{
        "waterTreatment":["Water_Treatment_Plant"],
        "reservior":["reserveLine"]
      },
      idd:""
    }
    this.zoomed = this.zoomed.bind(this)
  }

  handler=(doc)=>{
   
  }

  componentDidMount() {
    //Detect element on double click
/*     document.getElementById("clarifier").addEventListener("dblclick", (e) => {
      console.log(e.currentTarget.id);
      document.getElementById("currentElement").innerText = `${e.currentTarget.id}`

    })

    document.getElementById("reservior").addEventListener("dblclick", (e) => {
      console.log(e.currentTarget.id);
      document.getElementById("currentElement").innerText = `${e.currentTarget.id}`

    }) */
    // Zoom 
  
      this.addHideClass(['Water_Treatment_Plant','Deatail_Screen'])
    //  this.AddDisplayClass('Distribution_Schematic')
   
   

    var zoom = d3.zoom()
      .scaleExtent([0.2, 20])
      .on("zoom", this.zoomed)
      var idddd = ""
    attachEventListeners([{id:'waterTreatment',eventName:'click'}],(e1)=>{
      console.log(e1.currentTarget.id)
    //  console.log(d3.event.transform.k )
      //zoomed()
     // idddd = e1.currentTarget.id
      this.setState({idd:e1.currentTarget.id})
    })
   

    

   

    // D3.js adding events on svg
    var svg = d3.select("#Layer_1")
      .call(zoom)
      // Disable Panning
      // .on("mousedown.zoom", null)
      // .on("touchstart.zoom", null)
      // .on("touchmove.zoom", null)
      // .on("touchend.zoom", null)
     // .on("wheel.zoom", null)

    // Set svg and zoom as global for use in reset button handler
    this.svg = svg;
    this.zoom = zoom

  }

addHideClass=(ids)=>{
  ids.forEach((id)=>{
    document.getElementById(id).classList.add("testHideShow");
  })
}

AddDisplayClass = (id)=>{
  document.getElementById(id).classList.add("display");

}

  zoomed=()=>{
    //If zoom is greater than 8 then redirect to child svg
    console.log(d3.event.transform.k)
    if (d3.event.transform.k >= 2) {
      console.log("removed");
      // setTimeout(() => {
      //   document.getElementById("App").style.display = "none"
      //   document.getElementById("clarifierSvg").style.display = "initial"
      // }, 720)
      console.log(this.state.idd)
      document.getElementById('Water_Treatment_Plant').classList.add("display");
      document.getElementById('Distribution_Schematic').classList.add("testHideShow");
    }

    if(d3.event.transform.k>=4){
      
      document.getElementById('Deatail_Screen').classList.add("display");

    }

    // zoom effect transition
     d3.select(`#container`).transition().duration(750)
      .attr("transform", d3.event.transform);
  }

  resetHandler = () => {
    this.svg.call(this.zoom.transform, d3.zoomIdentity.scale(1));
    document.getElementById("Water_Treatment_Plant").classList.remove("display")
    document.getElementById('Distribution_Schematic').classList.remove("testHideShow");
    document.getElementById('Deatail_Screen').classList.remove("display");


  }

  render() {
    return (
      <div style={{ overflow: "hidden", display: "flex", flexDirection: "column" }} id="main">
        {/* <button onClick={this.nextHandler}>next</button>
        <button onClick={this.previousHandler}>previous</button>
        <button onClick={this.detectHandler}>Id detect</button> */}
        <div style={{ height: "10%", width: "100%" }}>
          <button style={{ float: "left", display: "inline-block" }} id="reset" onClick={this.resetHandler}>Reset</button>
          <span style={{ float: "left", }} id="currentElement"></span>
        </div>



        <div onDoubleClick={this.zoomHandler} id="App" className="App" draggable={true} onDrag={this.dragHandler} onDragEnd={this.dragOver} onDragStart={this.dragStart} style={{ height: "100%", width: "100%" }} >

          <SvgComponent id="svg" />
        </div>

        <div id="clarifierSvg" className="App" draggable={true} onDrag={this.dragHandler} onDragEnd={this.dragOver} onDragStart={this.dragStart} style={{ height: "50%", width: "50%", display: "none" }} >
          <div style={{ position: "absolute", top: 20, right: "50%", float: "left" }}>
            <button id="reset" onClick={this.resetHandler}>Reset</button>
          </div>
          <ObjectSvg  handler={this.handler}/>
        </div>
      </div>
    );
  }
}

export default App;